<template>
  <div>
    <el-dialog
      :title="update == true ? '角色添加' : '菜单修改'"
      :visible.sync="dialogFormVisible"
    >
      <el-form :model="form">
        <el-form-item
          label="角色名称"
          :label-width="formLabelWidth"
          v-model="form.rolename"
        >
          <el-input v-model="form.rolename" autocomplete="off"></el-input>
        </el-form-item>

        <!-- 树形控价 -->
        <el-form-item label="角色权限">
            <el-tree
            ref="tree"
            :data="menuList"
            show-checkbox
            node-key="id"
            :props="defaultProps"
          >
          </el-tree>
          
        </el-form-item>

        <el-form-item label="状态">
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
            active-color="#13ce66"
            inactive-color="#ff4949"
          >
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="addRoleList" v-if="update"
          >添加</el-button
        >
        <el-button type="primary" @click="updateRole" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { indexRouters } from "../../../router/index";
import { alertSuccess, alertWarn } from "../../../utils/alert";
import { ListRoleAdd, updateRoleAct, roleinfo } from "../../../utils/request";
export default {
  props: ["update"],
  components: {},
  data() {
    return {
        defaultProps:{
            children: 'children',
            label: 'title'
        },
      dialogFormVisible: false,
      form: {
        rolename:"",
        menus:"[]",
        status:1
      },
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({
      menuList: "menuList"
    }),
  },
  methods: {
    ...mapActions({
      getMenuList: "getMenuList",
      getRoleList:"getRoleList"
    }),
    cancle(){
        this.dialogFormVisible=false;
        this.clearForm()
    },
    clearForm() {
      this.form = {
        rolename:"",
        menus:"[]",
        status:1
      };
    },
    addRoleList() {
    this.form.menus=JSON.stringify(this.$refs.tree.getCheckedKeys())
      this.dialogFormVisible = false;
      ListRoleAdd(this.form).then((res) => {
        this.getRoleList()
        this.clearForm();
      });
    },
    getRoleDetail(id) {
      roleinfo(id).then((res) => {
        this.form = res.data.list;
        this.form.id = id;
        this.$refs.tree.setCheckedKeys(JSON.parse(this.form.menus))
      });
    },
    updateRole() {
    this.form.menus=JSON.stringify(this.$refs.tree.getCheckedKeys())
      updateRoleAct(this.form).then((res) => {
        if (res.data.code == 200) {
          alertSuccess(res.data.msg);
          this.dialogFormVisible = false;
          this.clearForm();
          this.getRoleList();
        } else {
          alertWarn(res.data.msg);
        }
      });
    },
  },
    mounted() {
    if(this.menuList.length===0){
        this.getMenuList()
    }
  },
};
</script>
<style scoped>
</style>


