<template>
<div>
    <el-table
    :data="roleList"
    style="width: 100%;margin-bottom: 20px;"
    row-key="id"
    border
    :tree-props="{children: 'children', hasChildren: 'hasChildren'}">
    <el-table-column
      prop="id"
      label="角色编号"
      sortable
      width="180">
    </el-table-column>
    <el-table-column
      prop="rolename"
      label="角色名称"
      sortable
      width="180">
    </el-table-column>
    
    <el-table-column
      prop="status"
      label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1">启用</el-button>
          <el-button type="primary" v-if="scope.row.status == 2">禁用</el-button>
        </template>
    </el-table-column>
    <el-table-column
      prop="icon"
      label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">修改</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
          </template>
    </el-table-column>
  </el-table>
</div>
</template>
<script>
 import {mapGetters,mapActions} from 'vuex';
 import {roleDel} from '../../../utils/request'
 import {alertWarn,alertSuccess} from '../../../utils/alert'
 import {reqRoleList} from '../../../utils/request'
export default {
props: [],
components: {},
data() {
return {

};
},
computed: {
...mapGetters({
  roleList:"roleList"
}),
},
methods: {
...mapActions({
  getRoleList:"getRoleList"
}),
edit(id){
  this.$emit('update',id)
},
del(id){
  this.$confirm('是否确认删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            roleDel(id).then(res=>{
              if(res.data.code==200){
                alertSuccess(res.data.msg)
                this.getRoleList()
              }else{
                alertWarn(res.data.msg)
              }
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          }); 
        });
}
},
mounted() {
  this.getRoleList()
}
};
</script>
<style scoped>
</style>


